.hero {
  background-image: url("../../assets/IMG/Blue_car.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center;
  color: #fff;
  min-height: 515px;
}

.tittle-hero {
  padding: 114px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.tittle-hero_text {
  margin: 5px 0;
}

.tittle-hero_text h1 {
  font-size: 2.8rem;
  font-weight: 500;
}

.tittle-hero_text h6 {
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 26px;
  color: #f3f6fc;
  margin-top: 30px;
  max-width: 455px;
}

.hero-items-pag {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  min-width: 86px;
  min-height: 24px;
  position: relative;
  bottom: 45px;
}

.btn-contact {
  height: 43px;
  width: 166px;
  color: #fff;
  background-color: #3552a6;
  border-radius: 5px;
  border: 2px solid #f3f6fc;
  -webkit-box-shadow: none;
          box-shadow: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 24px;
  padding-right: 10px;
  margin: 30px;
  font-size: 1.4rem;
  font-weight: 400;
}
.hero-items-pag .arrow-left {
  height: 17px;
  width: 30px;
  margin: 7px;
}
.hero-items-pag .arrow-right {
  height: 17px;
  width: 30px;
  margin: 7px;
}

.hero-items-pag p {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 500;
  z-index: 200;
}

@media (min-width: 1024px) {
  .tittle-hero {
    padding: 300px 0;
  }

  .tittle-hero_text h1 {
    font-size: 4rem;
  }

  .btn-contact {
    margin-top: 23px;
  }
}



